.avatar {
    position: relative;
    width: 50px;
    height: 50px;
    min-height: 50px;
    min-width: 50px;
    border-radius: 50%;
    background: #f8f8f8;
    overflow: hidden;
}

.avatar img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.avatarWrapper {
    grid-column: 1;
    grid-row: 1;
}
